<script>
export default {
    name:"page-hero",

    methods: {
    handleScroll: function (evt, el) {
      if (window.scrollY > 80) {
        el.setAttribute(
          'style',
          'opacity: 1; transform: rotate(180deg)'
        )
      }
      if (window.scrollY < 80) {
         el.setAttribute(
          'style',
          'opacity: 1; transform: rotate(18deg)'
        )
      }
      
    }
  }
}
</script>

