<template>
	<transition name="preload">
		<div v-if="show" class="preloader">
			<div class='load-container'>
				<img src="img/TIMVMA.png" alt="">
			</div>
			

		</div>
	</transition>
</template>

<script>
export default {

	name: 'preloader',
	data(){
		return {
			show: true
		}
	},
	mounted(){
		if((this.show)) this.showToggle()
	},
	methods: {
		showToggle(){
			setTimeout(() => {
				this.show = false
			}, 3000)
		}
	}

}

</script>

<style lang="scss">

.preloader {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	position: fixed;
	width: 100%;
	height: 100vh;
	background-color: #000;
	z-index: 9999;
	.load-container {

		width:200px;
		overflow:hidden;
		animation: pre-loading 2s linear;
		img {
			width:200px;
		}
	}
	
}

@keyframes pre-loading {
	0% {width:0px;}
	15% {width:30px;}
	20% {width:41px;}
	48% {width:79px;}
	70% {width:160px;}
	100% {width:200px;}
}


.preload-enter-active, .preload-leave-active {
	transition: opacity 1s;
}
.preload-enter, .preload-leave-to {
  opacity: 0;
}

</style>